.settings-page {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif;
    position: relative;
    min-height: 100vh;
}

.settings-title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
}

.settings-section {
    margin-bottom: 30px;
}

.settings-subtitle {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #555;
}

.settings-input {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-top: 5px;
    margin-bottom: 10px;
}

.settings-category-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.settings-category-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 10px;
}

.settings-checkbox-label {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #555;
}

.settings-checkbox {
    margin-right: 5px;
}

.settings-button {
    padding: 8px 12px;
    font-size: 14px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.settings-add-button {
    background-color: #4caf50;
    color: white;
}

.settings-add-button:hover {
    background-color: #45a049;
}

.settings-remove-button {
    background-color: #f44336;
    color: white;
}

.settings-remove-button:hover {
    background-color: #e53935;
}

.settings-nav-button {
    background-color: #2196f3;
    color: white;
}

.settings-nav-button:hover {
    background-color: #1976d2;
}

.settings-footer {
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    text-align: center;
}

.settings-reset-button {
    background-color: #f44336;
    color: white;
    padding: 6px 12px;
    font-size: 12px;
}

.settings-reset-button:hover {
    background-color: #e53935;
}